export default {
	data() {
		return {
			clientHeight: '',
			avatarUrl: this.$store.state.userInfo.avatarUrl,
			customTheme: '',
			type: 0,
			orderTab: [{
					name: '普通',
				},
				{
					name: '活动',
				},
			],
			list: [],
			footOption: {
				checkAll: 'cuIcon-round',
				delete: false
			},
			page1:1,
			page3:1,
			page4:1
		}
	},
	onLoad(e) {
		this.customTheme = this.$store.state.customTheme
		this.type = e.type
		console.log(this.type)
		this.collectionGetlist(e)
	},
	methods: {
		collectionGetlist(e) {
			if (this.type == 1) {
				this.$http({
					url: '/collection/items',
				}).then(res => {
					this.list = res
				})
			} else if (this.type == 3) {
				this.$http({
					url: '/order/distributions/'+this.page3,
				}).then(res => {
					for(let i of res){
						this.list.push(i)
					}
					this.page3 += 1
				})
			} else if (this.type == 4) {
				this.$http({
					url: '/message/list',
				}).then(res => {
					for (let i of res) {
						i.content = i.content.split('，')[0]
					}
					this.list = res
				})
			}
		},
		setting() {
			if (this.footOption.delete) {
				this.footOption.delete = false
			} else {
				this.footOption.delete = true
			}
		},
		allRead() {
			let i = this.list.map(a => a.message_id);
			this.$http({
				url: '/message/multiple',
				method: 'PUT',
				data: {
					messages: i
				},
				loading: 'none'
			}).then(res => {
				for (let j of this.list) {
					j.status = 1
				};
			})
		},
		rowClick(index, row) {
			if (!(row.status)) {
				this.$http({
					url: '/message/' + row.message_id,
					method: 'PUT',
					loading: 'none'
				}).then(res => {
					this.list[index].status = 1;
				})
			}
		},
		handleDelete(index, e, type) {
			switch (type) {
				case 1:
					this.$http({
						url: '/collection/item/' + e,
						method: 'DELETE',
						loading: '删除中'
					}).then(res => {
						this.list.splice(index, 1)
					})
					break;
				case 4:
					this.$http({
						url: '/message',
						method: 'DELETE',
						data: {
							messages: e
						},
						loading: '删除中',
					}).then(res => {
						this.list.splice(index, 1)
					})
				default:
					break;
			}

		},
		checkAll() {
			if (this.footOption.checkAll === 'cuIcon-round') {
				this.footOption.checkAll = 'cuIcon-roundcheckfill'
			} else {
				this.footOption.checkAll = 'cuIcon-round'
			}
		},
		itemJump(index, id) {
			console.log(index,id)
			uni.navigateTo({
				url: `../home/item?id=${id}`,
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		refund(e, index) {
			this.$http({
				url: '/order/' + e + '/refund',
				method: 'PUT',
				loading: 'none'
			}).then(res => {
				this.list[index].status = -2
				uni.showToast({
					title: '已申请退款',
					duration: 2000,
				});
			})
		}
	}
}
